<template>
  <div>
    <van-goods-action-button
      style="margin-top:100px"
      type="danger"
      text="立即购买"
      @click="onClickButton"
    />
    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      :goods-id="goodsId"
      :quota="quota"
      :quota-used="quotaUsed"
      :hide-stock="sku.hide_stock"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
    />
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      show: false,
      goods: {},

      goodsId: "",
      quota: 0,
      quotaUsed: 0,

      sku: {
        // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
        // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
        tree: [],

        // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
        list: []
      }
    };
  },
  computed: {},
  created() {
    this.getList();
  },
  mounted() {},
  watch: {},
  methods: {
    getList() {
      this.$axios
        .get("http://api.deiyoudian.com/api/goods/buyer/v1/goods/get", {
          params: {
            id: 122326
          },
          headers: {
            "Content-Type": "application/json",
            Platform: "Y%2fnOm9Q6SjVaSxnxNdJgeQ%3d%3d",
            Intercept: "y9H9hVFbMnL3iurHI3HWlg%3d%3d",
            Environment:
              ""
          }
        })
        .then(res => {
          this.sku.price = res.data.response.min_price;
          res.data.response.skus.forEach((item, index) => {
            this.sku.list.push({
              id: item.id,
              s1: index + "s1",
              s2: index + "s2",
              s3: index + "s3",
              price: item.price * 100,
              stock_num: item.stock_num
            });

            item.goods_specs.forEach((v, vI) => {
              let keyId = this.sku.tree.find(t => {
                return t.k == v.key;
              });

              if (!keyId) {
                this.sku.tree.push({
                  k: v.key,

                  k_s: "s" + (vI + 1),
                  v: [
                    {
                      id: index + "s" + vI, // skuValueId：规格值 id
                      name: v.value, // skuValueName：规格值名称
                      imgUrl: v.picture // 规格类目图片，只有第一个规格类目可以定义图片
                      //   previewImgUrl: v.picture,// 用于预览显示的规格类目图片
                    }
                  ]
                });
              } else {
                let vId = keyId.v.find(q => {
                  return q.name == v.value;
                });
                if (!vId) {
                  keyId.v.push({
                    id: index + "s" + vI,
                    name: v.value,
                    imgUrl: v.picture
                  });
                }
              }

              let treeId = this.sku.tree.find(vt => {
                return vt.k == v.key;
              });

              if (treeId) {
                let valueId = treeId.v.find(ti => {
                  return ti.name == v.value;
                });

                if (valueId) {
                  this.sku.list[index]["s" + (vI + 1)] = valueId.id;
                }
              }
            });
          });
        });
    },

    onClickButton() {
      this.show = true;
    },
    onAddCartClicked() {},
    onBuyClicked() {}
  },
  components: {}
};
</script>

<style scoped lang="scss">
</style>
